<template>
  <div class="ZyjAppRightBoxBodyA">
    <div class="bodyA">
      <div class="titlebox" ref="frontendTitle">
        <h1>
          前端开发 <button @click="toggleBlock(1)" :data-expanded="blockStatus[1] ? 'true' : 'false'">{{ getText(1) }}</button>

        </h1>
      </div>

      <ZyjAppRightBoxBodyBox v-for="item in qditems" :key="item.id" :name="item.name" :imgurl="item.imgurl"
        :bodytext="item.bodytext" :zpkf="item.zpkf" :wdgz="item.wdgz" :vidourl="item.vidourl" v-show="blockStatus[1]">
      </ZyjAppRightBoxBodyBox>
    </div>

    <div class="bodyA">
      <div class="titlebox" ref="programmingTitle">
        <h1>
          编程相关 <button @click="toggleBlock(2)" :data-expanded="blockStatus[2] ? 'true' : 'false'">{{ getText(2) }}</button>

        </h1>
      </div>

      <ZyjAppRightBoxBodyBox v-for="item in bcitems" :key="item.id" :name="item.name" :imgurl="item.imgurl"
        :bodytext="item.bodytext" :zpkf="item.zpkf" :wdgz="item.wdgz" :vidourl="item.vidourl" v-show="blockStatus[2]">
      </ZyjAppRightBoxBodyBox>
    </div>
    <div class="bodyA">
      <div class="titlebox" ref="gameDevTitle">
        <h1>
          游戏开发 <button @click="toggleBlock(3)" :data-expanded="blockStatus[3] ? 'true' : 'false'">{{ getText(3) }}</button>

        </h1>
      </div>
      <ZyjAppRightBoxBodyBox v-for="item in gmitems" :key="item.id" :name="item.name" :imgurl="item.imgurl"
        :bodytext="item.bodytext" :zpkf="item.zpkf" :wdgz="item.wdgz" :vidourl="item.vidourl" v-show="blockStatus[3]">
      </ZyjAppRightBoxBodyBox>
    </div>
    <div class="bodyA">
      <div class="titlebox" ref="designTitle">
        <h1>
          3D设计与建模 <button @click="toggleBlock(4)" :data-expanded="blockStatus[4] ? 'true' : 'false'">{{ getText(4) }}</button>

        </h1>
      </div>

      <ZyjAppRightBoxBodyBox v-for="item in jmitems" :key="item.id" :name="item.name" :imgurl="item.imgurl"
        :bodytext="item.bodytext" :zpkf="item.zpkf" :wdgz="item.wdgz" :vidourl="item.vidourl" v-show="blockStatus[4]">
      </ZyjAppRightBoxBodyBox>
    </div>
    <div class="bodyA">
      <div class="titlebox" ref="otherTitle">
        <h1>
          其他 <button @click="toggleBlock(5)" :data-expanded="blockStatus[5] ? 'true' : 'false'">{{ getText(5) }}</button>

        </h1>
      </div>

      <ZyjAppRightBoxBodyBox v-for="item in otheritems" :key="item.id" :name="item.name" :imgurl="item.imgurl"
        :bodytext="item.bodytext" :zpkf="item.zpkf" :wdgz="item.wdgz" :vidourl="item.vidourl" v-show="blockStatus[5]">
      </ZyjAppRightBoxBodyBox>
    </div>

  </div>
</template>
  
<script>
import { EventBus } from '../eventBus';
import ZyjAppRightBoxBodyBox from "./ZyjAppRightBoxBodyBox.vue";

export default {
  name: "ZyjAppRightBoxBodyA",
  data() {
    return {
      blockStatus: {
        1: true,
        2: true,
        3: true,
        4: true,
        5: true,
      },
      qditems: [
        {
          id: 1,
          name: "AI绘画(第⼆⼗⼆届浙江省⼤学⽣多媒体作品设计竞赛 交互课件(vue)项目)",
          imgurl: [
            require("../assets/img/aihuihua/001.png"),
            require("../assets/img/aihuihua/002.png"),
          ],
          bodytext:
            "人工智能从最初的模拟人类思考方式，到现在能够自主学习和决策，人工智能(AI)已经取得了令人瞩目的进步。在全球范围内，AI被视为新一轮科技革命和产业变革的重要驱动力。各国政府纷纷出台政策，推动AI的发展，并在教育、医疗、交通等领域进行广泛应用。",
          zpkf: "项目使用了Vue.js作为前端开发框架，利用了Vuex、SCSS和Vue Router等插件来增强前端应用的功能和可维护性。后端部分采用了JSON格式来处理数据。这些技术和工具的综合使用有助于我们构建出现代化、响应式且高性能的Web应用程序。",
          wdgz: "我担任4人队伍的队长，主要负责领导团队，内容策划和部分vue技术实现",
          vidourl: "video1.mp4",
        },
        
      ],
      bcitems: [
       
      ],
      gmitems: [],
      jmitems: [],
      otheritems: []
    };
  },
  created() {
    EventBus.$on('scrollToSection', this.scrollToSection);
    // 读取localStorage中的blockStatus，如果不存在则使用默认值
    try {
      this.blockStatus =
        JSON.parse(localStorage.getItem("blockStatus")) || this.blockStatus;
    } catch (error) {
      console.error("Error reading blockStatus from localStorage:", error);
    }
  },
  methods: {

    scrollToSection(refName) {
      const refElement = this.$refs[refName];
      if (refElement) {
        refElement.scrollIntoView({ behavior: 'smooth' });
      }
    },
    toggleBlock(blockId) {
      this.$set(this.blockStatus, blockId, !this.blockStatus[blockId]);
      try {
        localStorage.setItem("blockStatus", JSON.stringify(this.blockStatus));
      } catch (error) {
        console.error("Error saving blockStatus to localStorage:", error);
      }
    },

    getText(blockId) {
      return this.blockStatus[blockId] ? "收起" : "展开";
    },
  },
  components: {
    ZyjAppRightBoxBodyBox,
  },
};
</script>
  
  
<style lang="css" scoped>
.ZyjAppRightBoxBodyA {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;


}

.titlebox {
  font-family: Arial, Helvetica, sans-serif;
  text-align: center;
  height: 100px;
  line-height: 100px;
  width: 400px;
  background-color: #ffffff56;
  margin-top: 20px;
  margin-bottom: 20px;
}

.bodyA {
  width: 100%;
  background-color: #c5c2c23d;
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
}

button {
  margin-left: 60px;
  border: none;
  border-radius: 10px;
  font-size: medium;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  background-color: #fff;
  height: 50px;
  width: 100px;
  float: right;
  margin: 20px;
  margin-top: 25px;
}
button[data-expanded="true"] {
  background-color: #dbfadb8c; /* 展开状态的背景色 */
}

button[data-expanded="false"] {
  background-color: #ffaaaa54; /* 收起状态的背景色 */
}

.ZyjAppRightBoxBodyBox {
  display: block;
}
</style>